
/* ************** RESPONSIVE BOOTSTRAP5 DATATABLES OPEN ************** */

table.dataTable.dtr-inline.collapsed {
    >tbody {
        >tr {
            >td.child {
                cursor: default !important;

                &:before {
                    display: none !important;
                }
            }

            >th.child {
                cursor: default !important;

                &:before {
                    display: none !important;
                }
            }

            >td.dataTables_empty {
                cursor: default !important;

                &:before {
                    display: none !important;
                }
            }

            >td.dtr-control {
                position: relative;
                padding-inline-start: 30px;
                cursor: pointer;

                &:before {
                    inset-block-start: 50%;
                    inset-inline-start: 5px;
                    height: 1em;
                    width: 1em;
                    margin-block-start: -9px;
                    display: block;
                    position: absolute;
                    color: white;
                    border: .15em solid white;
                    border-radius: 1em;
                    box-shadow: 0 0 .2em #444;
                    box-sizing: content-box;
                    text-align: center;
                    text-indent: 0 !important;
                    font-family: "Courier New", Courier, monospace;
                    line-height: 1em;
                    content: "+";
                    background-color: $primary;
                }
            }

            >th.dtr-control {
                position: relative;
                padding-inline-start: 30px;
                cursor: pointer;

                &:before {
                    inset-block-start: 50%;
                    inset-inline-start: 5px;
                    height: 1em;
                    width: 1em;
                    margin-block-start: -9px;
                    display: block;
                    position: absolute;
                    color: white;
                    border: .15em solid white;
                    border-radius: 1em;
                    box-shadow: 0 0 .2em #444;
                    box-sizing: content-box;
                    text-align: center;
                    text-indent: 0 !important;
                    font-family: "Courier New", Courier, monospace;
                    line-height: 1em;
                    content: "+";
                    background-color: $primary;
                }
            }
        }

        >tr.parent {
            >td.dtr-control {
                &:before {
                    content: "-";
                    background-color: #d33333;
                }
            }

            >th.dtr-control {
                &:before {
                    content: "-";
                    background-color: #d33333;
                }
            }
        }
    }
}

table.dataTable.dtr-inline.collapsed.compact {
