table.dataTable {
    >tbody {
        >tr.child {
            padding: .5em 1em;

            &:hover {
                background: transparent !important;
            }

            ul.dtr-details {
                display: inline-block;
                list-style-type: none;
                margin: 0;
                padding: 0;

                >li {
                    border-block-end: 1px solid #efefef;
                    padding: .5em 0;

                    &:first-child {
                        padding-block-start: 0;
                    }

                    &:last-child {
                        border-block-end: none;
                    }
                }
            }

            span.dtr-title {
                display: inline-block;
                min-width: 75px;
                font-weight: bold;
            }
        }
    }
}

div.dtr-modal {
    position: fixed;
    box-sizing: border-box;
    inset-block-start: 0;
    inset-inline-start: 0;
    height: 100%;
    width: 100%;
    z-index: 100;
    padding: 10em 1em;

    div.dtr-modal-display {
        position: absolute;
        inset-block-start: 0;
        inset-inline-start: 0;
        inset-block-end: 0;
        inset-inline-end: 0;
        width: 50%;
        height: 50%;
        overflow: auto;
        margin: auto;
        z-index: 102;
        overflow: auto;
        background-color: #f5f5f7;
        border: 1px solid black;
        border-radius: .5em;
        box-shadow: 0 12px 30px rgba(0, 0, 0, 0.6);
    }

    div.dtr-modal-content {
        position: relative;
        padding: 1em;
    }

    div.dtr-modal-close {
        position: absolute;
        inset-block-start: 6px;
        inset-inline-end: 6px;
        width: 22px;
        height: 22px;
        border: 1px solid #eaeaea;
        background-color: #f9f9f9;
        text-align: center;
        border-radius: 3px;
        cursor: pointer;
        z-index: 12;

        &:hover {
            background-color: #eaeaea;
        }
    }

    div.dtr-modal-background {
        position: fixed;
        inset-block-start: 0;
        inset-inline-start: 0;
        inset-inline-end: 0;
        inset-block-end: 0;
        z-index: 101;
        background: rgba(0, 0, 0, 0.6);
    }
}

div.dtr-bs-modal {
    table.table {
        tr {
            &:first-child {
                td {
                    border-block-start: none;
                }
            }
        }
    }
}

@media screen and (max-width: 767px) {
    div.dtr-modal {
        div.dtr-modal-display {
            width: 95%;
        }
    }
}

#example2 {
    .parent {
        animation: none;
    }
}


table.dataTable {
    margin: 0 auto;
    width: 100% !important;
}
